import React from 'react'
import Link from 'gatsby-link'
import link from '../utils/link';

export default ({ index, pageCount }) => {
  let pageItem = []
  for (var i = Math.max(1, index-2); i <= Math.min(index+2, pageCount); i++) {
    pageItem.push(
      <li className={`page-item ${index === i? 'disabled': ''}`} key={i}>
        <Link className="page-link" to={link.blogList(i)}>{i}</Link>
      </li>
    )
  }
  return (
    <nav aria-label="Page navigation">
      <ul className="pagination">
        <li className={`page-item ${index === 1? 'disabled': ''}`}>
          <Link className="page-link" to={link.blogList(index-1)} aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
            <span className="sr-only">Previous</span>
          </Link>
        </li>
        {pageItem}
        <li className={`page-item ${index === pageCount? 'disabled': ''}`}>
          <Link className="page-link" to={link.blogList(index+1)} aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
            <span className="sr-only">Next</span>
          </Link>
        </li>
      </ul>
    </nav>
  )
}



